.header {
  z-index: 5;
  width: 100%;

  margin-top: 2.5rem;
}

.bg.header {
  position: absolute;

  .header__wrapper {
    margin-bottom: 0;
    padding-bottom: 0;
    border-bottom: none;
  }
}

.bg.header * {
  color: #fff;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 3.6rem;
  padding-bottom: 2.2rem;
  border-bottom: 1px solid #C4C4C4;
}

.header__phone-wrapper {
  font-family: @roboto;
  text-align: right;
}

.header__phone {
  display: inline-block;

  margin-bottom: 0.8rem;
  font-size: 2.4rem;
  font-weight: 500;
}

.header__phone-text {
  font-size: 1.8rem;
  font-weight: 300;
}

.header__menu-toggle,
.header__link-call-back {
  display: none;
}

@media (max-width: @mobile-width) {
  .header {
    height: @a;
    margin-top: 0;
    padding-top: @m54px;
    .container {
      width: @a;
    }
    .logo__description {
      display: none;
    }
  }

  .active.header {
    min-height: 100vh;      //   js 
    overflow-y: scroll; //   js 
    overflow-x: hidden; //   js 
  }

  .no-js.header {
    position: relative;
    height: 200vw; // 1500px;
    overflow: visible;
  }

  .no-js.header,
  .active.header {
    // height: 100vh;      //   js 
    // overflow-y: scroll; //   js 
    // overflow-x: hidden; //   js 

    // position: relative;
    z-index: 10;
    background-color: #303030;

    .header__wrapper {
      justify-content: flex-start;
      flex-wrap: wrap;
    }

    .header__link-call-back,
    .header__main-nav {
      display: block;
    }

    .header__phone-wrapper {
      display: none;
    }
  }

  .active.header {
    position: fixed;
  }

  .active.header * {
    color: #fff;
  }

  .header.bg .header__wrapper,
  .header__wrapper {
    margin-bottom: @m52px;
    border-bottom: none;
  }

  .header__menu-toggle {
    display: block;
  }

  .header__link-call-back,
  .header__main-nav {
    display: none;
  }

  .header__logo {
    margin-left: @m33px;
    margin-right: auto;
  }

  .header__main-nav {
    margin-top: @m52px;
    padding-left: @m5px;
  }

  .header__phone {
    margin-bottom: @m8px;
    font-size: @m24px;
  }

  .header__phone-text {
    font-size: @m18px;
  }

  .header__link-call-back {
    width: @m64px;
    height: @m62px;
  }

  .header__link-call-back-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
